  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.spinner {
  z-index: 11;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(47, 48, 58, .3), rgba(47, 48, 58, .3)), url("cinema-beckground.5306c29a.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: fixed;
  top: 0;
  left: 0;
}

@media screen and (min-width: 768px) {
  .spinner {
    background-position: 54% 0;
  }
}

@media screen and (min-width: 1280px) {
  .spinner {
    background-position: 0 10%;
  }
}

.spinner__overlay {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
  .spinner__overlay {
    transform: translate(-40%, -50%);
  }
}

@media screen and (min-width: 1280px) {
  .spinner__overlay {
    transform: translate(-40%, -10%);
  }
}

.spinner__pic {
  animation: spinner 1.7s linear infinite;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.spinner__text {
  text-align: center;
  letter-spacing: .03em;
  margin-top: 20px;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.25;
}

@media screen and (min-width: 768px) {
